и их способностей, становлении личности. Консультативная работа состоит в оказании помощи педагогам, родителям, детям по широкому кругу личностных, профессиональных и других конкретных жизненных проблем.")]),_c('span',{staticClass:"text-long"},[_c('span',{staticClass:"text-str"},[_vm._v("Цель психологической службы")]),_vm._v(" — обеспечение психологических условий, способствующих максимальному психическому и личностному развитию каждого школьника.")]),_c('div',{staticClass:"psychologist"},[_c('img',{staticClass:"psychologist-photo",attrs:{"src":require("../assets/images/psychologist.jpg"),"alt":"psychologist"}}),_c('div',{staticClass:"psychologist-info"},[_c('p',{staticClass:"text-str text-centors"},[_vm._v("Психолог УВК ШГ № 38 Суюмбаева Асель Таласовна")]),_c('span',{staticClass:"text"},[_c('span',{staticClass:"text-str"},[_vm._v("Основная идея психологической службы —")]),_vm._v(" идея сопровождения, которое должно быть направлено на создание психологических условий для успешного обучения и психологического развития.")]),_c('span',{staticClass:"text"},[_c('span',{staticClass:"text-str"},[_vm._v("Главный принцип организации психологической службы —")]),_vm._v(" системность, т.е. психолог должен реализовывать все 5 функции и делать это систематически (составить рабочий план)")]),_c('span',{staticClass:"text-str"},[_vm._v("Стратегии психологической работы в школе")]),_c('span',{staticClass:"text"},[_vm._v("В самом общем виде задачу психолога в школе можно обозначить как психологическое обеспечение педагогической деятельности. Как, каким образом такое обеспечение может строиться? Мы предлагаем выделить две стратегии, два основных направления психологической деятельности в школе. Их можно обозначить как психологическое консультирование и проектирование (или «конструирование»).")]),_c('span',{staticClass:"text"},[_vm._v("Школьная психологическая служба должна осуществлять работу, направленную на исправление уже сложившейся ситуации, а также создавать программы, которы